html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
html,
body {
  padding: 0;
  margin: 0;
  max-width: 100%;
  width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
::selection {
  background: $cloud-burst;
  color: white;
}

//Alignements
.align-right {
  float: right;
}
.align-left {
  float: left;
}
.align-center {
  position: relative;
  margin: 0 auto;
  display: table;
}

//liens
a {
  transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
  &:hover {
    color: $cloud-burst;
  }
}

//Typographies
hr {
  position: relative;
  border-bottom: 0;
  border-top: 0.1rem solid $heather;
  margin: 2rem 0;
}
strong {
  font-family: $bold-heebo;
  font-weight: 400;
}
span.link {
  font-family: $regular-heebo;
  font-size: 1.3rem;
  color: $denim;
  cursor: pointer;
  &:hover {
    color: $cloud-burst;
  }
}
p,
ul li,
ol li,
label,
input,
button,
h1,
h2,
h3,
h4,
h5,
h6 {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 150%;
}
p {
  color: $cloud-burst;
  font-family: $regular-heebo;
  font-size: $font-size-base;
}
h1 {
  font-family: $bold-poppins;
  font-size: $h1-size;
  letter-spacing: -0.01em;
  line-height: 140%;
  &.headline {
    color: white;
  }
}
h2 {
  font-size: $h2-size;
  font-family: $light-poppins;
}
h3 {
  font-size: $h3-size;
  font-family: $regular-poppins;
}
h4 {
  font-size: $h4-size;
  font-family: $bold-poppins;
  color: $cloud-burst;
  &.light {
    font-family: $light-poppins;
    color: $cloud-burst;
    font-size: 1.5rem;
  }
}
h5 {
  font-size: $h5-size;
}

//Forms
label {
  color: $cloud-burst;
  font-family: $bold-heebo;
  font-size: $font-size-base;
}
input[type="text"],
input[type="number"],
input[type="email"],
input[type="password"] {
  position: relative;
  margin: 0.5rem 0 0 0;
  padding: 0;
  height: 4rem;
  min-width: 45rem;
  line-height: 4rem;
  font-family: $regular-heebo;
  font-size: $font-size-base;
  color: $raven;
  border: 0;
  border-bottom: 0.1rem solid $heather;
  box-shadow: none;
  transition: all 0.2s cubic-bezier(0.045, 0.715, 0.25, 1);
  &.error {
    color: $valencia;
    border-bottom: 0.1rem solid $valencia;
    &:hover,
    &:focus,
    &:active {
      border-bottom: 0.1rem solid $valencia;
    }
  }
  &::placeholder {
    color: $raven !important;
  }
  &:focus,
  &:active {
    border: 0;
    border-bottom: 0.1rem solid $raven;
    box-shadow: none;
    transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
  }
  &:focus,
  &:active {
    color: $cloud-burst;
  }
  &.search {
    position: fixed;
    top: 10.2rem;
    margin-left: -18.8rem;
    width: 29.4rem;
    min-width: 29.4rem;
    border-radius: 2rem;
    border: 0.1rem solid transparent;
    background-color: $athens-gray;
    padding-left: 6rem;
    background-image: url("/icons/recherche.svg");
    background-position: 1.5rem 55%;
    background-repeat: no-repeat;
    z-index: 3;
    &:focus,
    &:active {
      border: 0.1rem solid $heather;
    }
  }
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
//Container
.container {
  position: relative;
  margin: 0 auto;
  display: table;
  max-width: 153rem;
  width: 100%;
  &.container-suspense {
    margin: 6rem auto 0 auto;
    p {
      font-size: $h2-size;
    }
  }
  &.empty {
    margin-top: 8rem;
    * {
      position: relative;
      margin: 0 auto;
      display: table;
      max-width: 28rem;
      width: 100%;
    }
    .icon {
      margin-bottom: 1rem;
      max-width: 12rem;
      height: auto;
      width: auto;
    }
    p {
      &:first-child {
        color: $cloud-burst;
      }
      &:last-child {
        margin-top: 0.5rem;
        color: $raven;
        font-size: 1.3rem;
      }
    }
  }
  &.candidats {
    margin-top: 2rem;
  }
  &.candidats-selectionnes {
    p {
      &:nth-of-type(1) {
        color: white;
      }
      &:last-child {
        color: white;
      }
    }
  }
}
.columns {
  float: none;
  display: inline-block;
  vertical-align: top;
}
.row-fluid {
  h3 {
    margin-bottom: 1.5rem;
  }
  [class*="large-4"] {
    &:nth-of-type(1) {
      padding: 0 1.5rem 0 0;
    }
    &:nth-of-type(2) {
      padding: 0 1.5rem;
    }
    &:nth-of-type(3) {
      padding: 0 0 0 1.5rem;
    }
  }
  [class*="large-3"] {
    &:nth-child(1n + 1) {
      padding: 0 1.5rem;
      margin-bottom: -0.5rem;
      float: left;
    }
    .box-encart {
      margin-bottom: 3rem;
    }
  }
}

.loader {
  position: relative;
  display: table;
  margin: 0 auto;
}

//Loader
#loader span {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  margin: 35px 5px;
  opacity: 0;
  &:nth-child(1) {
    animation: opacitychange 1s ease-in-out infinite;
    background-color: $denim;
  }
  &:nth-child(2) {
    animation: opacitychange 1s ease-in-out 0.33s infinite;
    background-color: $casablanca;
  }
  &:nth-child(3) {
    animation: opacitychange 1s ease-in-out 0.66s infinite;
    background-color: $raven;
  }
}

@keyframes opacitychange {
  0%,
  100% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
}

//Popup
.overlay-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(34, 57, 79, 0);
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.045, 0.715, 0.25, 1);
  z-index: 5;
  &.open {
    background-color: rgba(34, 57, 79, 0.5);
    pointer-events: auto;
  }
}
.wrapper-popup {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  pointer-events: none;
  z-index: 5;
}
.popup {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  top: 0;
  right: 0;
  padding: 11.5rem 3rem;
  background-color: white;
  max-width: 43rem;
  width: 100%;
  height: 100vh;
  transform: translateX(100%);
  transition: all 0.4s cubic-bezier(0.045, 0.715, 0.25, 1);
  z-index: 7;
  pointer-events: auto;
  overflow-y: scroll;
  &.open {
    display: block;
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
  }
  &.center {
    position: relative;
    top: 0;
    right: auto;
    padding: 4rem 3.5rem 1.5rem 3.5rem;
    max-width: 43rem;
    width: 100%;
    height: auto;
    border-radius: 1rem;
    transform: translateX(0) translateY(-10%);
    &.open {
      transform: translateY(0%);
    }
    button {
      position: relative;
      margin: 0 auto;
      display: table;
      left: 0rem;
      &:nth-of-type(1) {
        margin-top: 2rem;
      }
    }
    p {
      color: $raven;
    }
    h4 {
      font-family: $regular-poppins;
      span {
        font-family: $bold-poppins;
      }
    }
    .box-note-popup {
      position: relative;
      margin-top: 1.5rem;
      padding: 2rem;
      background-color: $athens-gray;
      border-radius: 1rem;
      overflow: hidden;
      z-index: 2;
      &::after {
        position: absolute;
        bottom: -1.5rem;
        right: -1rem;
        background-image: url("/icons/vector-encart-2.svg");
        background-size: 10rem;
        width: 10rem;
        height: 11rem;
        content: "";
        z-index: 1;
      }
      strong,
      p {
        color: $cloud-burst;
      }
      p {
        margin: 0;
      }
    }
  }
  label {
    margin-top: 3rem;
  }
  button {
    position: absolute;
    right: 3rem;
    margin-top: 4rem;
  }
  input[type="text"],
  input[type="number"],
  input[type="email"],
  input[type="password"] {
    min-width: 100%;
  }
  .note {
    color: $raven;
    margin-top: 10rem;
  }
  .close {
    position: relative;
    left: -0.2rem;
    margin-bottom: 5rem;
    cursor: pointer;
    &:hover {
      filter: brightness(50%) sepia(1) hue-rotate(165deg) saturate(5);
    }
  }
}

//Common box's
.box-note {
  position: relative;
  padding: 4rem 3rem;
  border-radius: 1rem;
  background: $denim;
  background-image: url("/icons/vector-encart-1.svg");
  background-position: 110% -15%;
  background-repeat: no-repeat;
  background-size: 18rem;
  overflow: hidden;
  &::after {
    position: absolute;
    bottom: 0;
    right: 0;
    background-image: url("/icons/vector-3.svg");
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: 18rem;
    width: 100%;
    height: 100%;
    content: "";
  }
  p {
    position: relative;
    margin: 0;
    font-size: 2.1rem;
    color: white;
    z-index: 5;
    span {
      font-family: $bold-poppins;
    }
    &.sub-note {
      margin-top: 2rem;
      font-size: $font-size-base;
    }
  }
}
.box-candidats-cooptes,
.box-views-small,
.box-points {
  background-color: $solitaire;
  padding: 0 1rem 0 4.4rem;
  height: 4rem;
  line-height: 4rem;
  font-family: $regular-heebo;
  font-size: 1.3rem;
  border-radius: 0.5rem;
  color: $raven;
  span {
    padding-right: 0.3rem;
    font-family: $bold-poppins;
    color: $cloud-burst;
    font-size: $font-size-base;
  }
}
.box-points {
  background-color: $tara;
  padding-left: 5.2rem;
  &::before {
    position: absolute;
    margin: 0.6rem 0 0 -4rem;
    background-image: url("/icons/cadeau.svg");
    background-repeat: no-repeat;
    background-size: 2.7rem;
    width: 2.7rem;
    height: 2.7rem;
    content: "";
  }
}
.box-views-small {
  &::before {
    position: absolute;
    margin: 0.9rem 0 0 -3.2rem;
    background-image: url("/icons/view.svg");
    background-repeat: no-repeat;
    background-size: 2.4rem;
    width: 2.4rem;
    height: 2.4rem;
    content: "";
  }
}

//Common
main:not(.login) {
  position: relative;
  margin-top: 7rem;
  background-color: rgba(242, 243, 245, 0);
  height: 100%;
  min-height: calc(100vh - 7rem);
  width: 100%;
  padding: 5rem 5rem 4.5rem 5rem;
  z-index: 1;
  transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
  &.background-athens-gray {
    background-color: rgba(242, 243, 245, 1);
  }
  p.react-switch {
    color: $raven;
    min-width: 8.5rem; //min-width pour éviter décalage du status et des points sur annonces
    .react-switch {
      top: 0.5rem;
      margin-right: 1.2rem;
    }
  }
}

//Login
main.login {
  [class*="large"] {
    height: 100vh;
    &:first-child {
      position: relative;
      padding: 6rem;
      width: 68.6rem;
      z-index: 4;
      .logo {
        position: absolute;
      }
      .box-item {
        display: flex;
        align-items: center;
        height: calc(100vh - 12.5rem);
        width: 100%;
        max-width: 45rem;
        label {
          margin-top: 4rem;
        }
        .note {
          margin: 2rem 0 5rem 0;
          font-size: 1.3rem;
          &.note-envoie-code {
            margin: 2rem 0 1rem 0;
          }
          &.note-connection-echec {
            color: $valencia;
            margin: 1rem 0 -1rem 0;
          }
        }
        &.step-2 button {
          margin-top: 5rem;
        }
      }
    }
    &:last-child {
      width: calc(100% - 68.6rem);
      background-color: $cloud-burst;
      img {
        position: absolute;
        z-index: 1;
        &.vector-1 {
          top: 0;
        }
        &.vector-2 {
          top: 3rem;
          right: 0;
        }
        &.vector-3 {
          bottom: 0;
          margin-left: 7rem;
        }
        &.vector-4 {
          bottom: 0;
          right: 0;
        }
      }
      .box-item {
        position: relative;
        padding: 0 10rem;
        display: flex;
        align-items: center;
        height: 100vh;
        z-index: 2;
        .box-text {
          margin-top: 4rem;
          padding: 3rem;
          width: 44rem;
          height: 14rem;
          background: white;
          border-radius: 1rem;
          p {
            &:nth-of-type(1) {
              color: $denim;
              font-family: $bold-poppins;
              font-size: 3.4rem;
              float: left;
              height: 5rem;
              width: 8.6rem;
            }
            &:nth-of-type(2) {
              margin-top: 0.5rem;
              float: right;
              width: calc(100% - 8.6rem);
              font-size: 1.3rem;
              a {
                color: $raven;
                &:hover {
                  color: $cloud-burst;
                }
              }
            }
          }
        }
      }
    }
  }
}

//Dashboard
main.dashboard {
  background-color: $athens-gray;
  .container {
    max-width: 150rem;
  }
  .row-fluid {
    position: relative;
    margin-top: 4rem;
    column-count: 3;
    column-gap: 3rem;
    .columns {
      padding: 0;
      &:nth-of-type(3) {
        display: table;
      }
    }
    h3:nth-of-type(1) {
      margin-top: -4.5rem;
    }
    button,
    a {
      position: absolute;
      right: 2rem;
    }
  }
  &::after {
    position: absolute;
    top: -7rem;
    right: 0;
    background-image: url("/icons/vector-5.svg");
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 26rem;
    width: 26rem;
    height: 100%;
    content: "";
    z-index: -1;
  }
  &::before {
    position: absolute;
    bottom: 0rem;
    left: 0;
    background-image: url("/icons/vector-6.svg");
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 15rem;
    width: 15rem;
    height: 100%;
    content: "";
    z-index: -1;
  }
  .icon {
    position: relative;
    margin: 0 2rem 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    float: left;
    &.iron {
      background-color: $iron;
    }
    &.link-water {
      background-color: $link-water;
    }
    &.iceberg {
      background-color: $iceberg;
    }
    &.half-spanish-white {
      background-color: $half-spanish-white;
    }
  }
  .box-note,
  .box-item {
    border-radius: 1rem;
    box-shadow: 0 0.2rem 0.8rem rgba(35, 52, 61, 0.1);
    margin-bottom: 3rem;
  }
  .box-text.demandes-recompenses {
    width: calc(100% - 10rem);
  }
  .box-views {
    position: relative;
    margin-bottom: 2rem;
    height: 9.5rem;
    width: 100%;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
      width: 100%;
    }
    p {
      position: relative;
      left: -3.8rem;
      text-align: center;
      &:nth-of-type(1) {
        font-family: $bold-poppins;
        font-size: 2.2rem;
        &::before {
          position: absolute;
          margin: 0.5rem 0 0 -3rem;
          background-image: url("/icons/view.svg");
          background-repeat: no-repeat;
          background-size: 2.4rem;
          width: 2.4rem;
          height: 2.4rem;
          content: "";
        }
      }
    }
  }
  .box-item {
    position: relative;
    width: 100%;
    display: inherit;
    padding: 2.65rem 2rem 0.65rem 2rem;
    background: white;
    &.cooptation,
    &.communication {
      button,
      a {
        top: 2rem;
      }
      .box-views {
        background: $solitaire;
      }
    }
    &.communication {
      .box-views {
        background: rgba(21, 90, 196, 0.1);
      }
    }
    h4 {
      margin-bottom: 2rem;
    }
    p {
      width: calc(100% - 8rem);
      float: right;
      margin: 0;
      &:first-child {
        margin-top: 0.5rem;
      }
      &:nth-child(2) {
        color: $raven;
        span {
          &:nth-of-type(1) {
            font-family: $bold-poppins;
            color: $cloud-burst;
            font-size: 2.3rem;
          }
          &:nth-of-type(2) {
            color: $raven;
            font-size: 1.3rem;
          }
        }
      }
    }
  }
}

//Cooptation
main.cooptation,
main.recompenses {
  .tab-annonces,
  .tab-candidats,
  .tab-candidats-archives {
    margin-top: 4rem;
  }
  &.background-athens-gray .react-tabs__tab-list {
    background: $athens-gray;
  }
  &.background-white .react-tabs__tab-list {
    background-color: white;
    display: relative;
  }
  .col-candidats-selectionnes {
    .box-rejeter {
      display: none;
    }
  }
  .opacity {
    opacity: 0.5;
    transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
  }
  .box-encart {
    padding: 2rem;
    margin-bottom: 1.5rem;
    background-color: white;
    border-radius: 1rem;
    box-shadow: 0 0.2rem 0.8rem rgba(35, 52, 61, 0.1);
    transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
    &:last-child {
      margin-bottom: 4rem;
    }
    .icon {
      margin-right: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      float: left;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      background-color: $link-water;
      color: $denim;
      text-transform: uppercase;
      font-size: $font-size-base;
      &.athens-gray {
        background-color: $athens-gray;
        color: $raven;
      }
    }
    .row-fluid.box-nom-titre {
      [class*="large"] {
        padding: 0;
        &:first-child {
          width: 5rem;
        }
        &:last-child {
          p {
            max-width: 20rem;
          }
          width: calc(100% - 5rem);
        }
      }
    }
    p {
      color: $raven;
      width: 100%;
      a {
        color: $raven;
      }
      &.email a:hover {
        color: $cloud-burst;
      }
      &.nom,
      &.titre,
      &.cooptes-par {
        line-height: 120%;
      }
      &.titre {
        padding-top: 0.8rem;
      }
      &:nth-of-type(1) {
        color: $cloud-burst;
        font-family: $bold-heebo;
        font-size: 1.7rem;
        margin: 0;
      }
      &:nth-of-type(2) {
        color: $cloud-burst;
        font-size: $font-size-base;
        margin: -0.5rem 0 1.5rem 0;
      }
      &:nth-of-type(3) {
        color: $cloud-burst;
        font-size: $font-size-base;
        span {
          font-family: $bold-heebo;
        }
      }
      &.cooptes-par {
        float: left;
        font-family: $regular-heebo;
        font-size: $font-size-base;
        span {
          font-family: $bold-heebo;
          &.comma {
            font-family: $regular-heebo;
          }
          span.last {
            display: none;
          }
        }
      }
      &.date {
        float: left;
        color: $raven;
        margin: 1rem 0;
        font-size: 1.3rem;
      }
      &:not(:nth-of-type(1)):not(:nth-of-type(2)):not(:nth-of-type(3)):not(.date) {
        margin: -0.6rem 0;
      }
    }
    .rejeter {
      margin-top: -1rem;
      transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
      &:hover {
        filter: brightness(115%);
      }
    }
  }
  .tab-candidats-archives {
    .box-encart {
      min-height: 20rem;
      margin-bottom: 3.5rem;
    }
    .row-fluid {
      padding: 0 1rem;
      column-gap: 1.5rem;
      column-count: 4;
      column-width: 35rem;
    }
    .columns {
      break-inside: avoid;
      .box-encart {
        column-count: 1;
      }
    }
  }
  .tab-candidats {
    .container-scroll:not(.candidats-selectionnes) {
      .box-encart {
        border: 0.2rem solid rgba(21, 90, 196, 0);
        transition: all 0.03s cubic-bezier(0.045, 0.715, 0.25, 1);
        &:hover {
          border: 0.2rem solid rgba(21, 90, 196, 0.5);
        }
      }
    }
    .container-scroll.candidats-selectionnes {
      .box-encart {
        cursor: auto;
      }
    }
    .box-item {
      height: calc(100vh - 22.5rem);
      padding: 2rem 0 4.2rem 0;
      background: $athens-gray;
      border: 0.1rem solid $heather;
      border-radius: 1rem;
      overflow: hidden;
      &.denim {
        background-color: $denim;
        border: 0;
        h4 {
          color: white;
        }
        .box-encart {
          padding: 2rem 2rem 3rem 2rem;
        }
      }
      h4 {
        margin: 0 0 2rem 2rem;
      }
    }
    .box-encart {
      margin-bottom: 1.5rem;
    }
    .container-scroll {
      padding: 0 2rem;
      overflow-y: scroll;
      height: 100%;
      overflow-x: hidden;
      scroll-behavior: smooth;
      &.droppable,
      &.droppable2,
      &.droppable3 {
        overflow-y: hidden;
      }
    }
  }
}

//Cooptation & Recompenses
main.cooptation,
main.recompenses {
  .box-annonce,
  .box-recompense,
  .box-demande {
    position: relative;
    margin: 0 auto;
    display: table;
    width: 100%;
    height: 9.8rem;
    border-bottom: 0.1rem solid $mischka;
    &:nth-of-type(1) {
      margin-top: 3rem;
    }
    p {
      font-size: 1.3rem;
      color: $raven;
    }
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style: none;
        display: inline-block;
      }
      &.titre {
        p {
          margin: 0 0 0 1rem;
          font-size: $font-size-base;
          color: $cloud-burst;
        }
        li.date {
          margin: 0;
          p {
            color: $raven;
          }
        }
      }
      &.details {
        li {
          margin: 0 4.5rem 0 0;
          &::before {
            position: absolute;
            margin: -0.1rem 0 0 -2rem;
            background-repeat: no-repeat;
            background-position: center left;
            background-size: 2rem;
            width: 2rem;
            height: 2rem;
            content: "";
          }
          &.star::before {
            background-image: url("/icons/star.svg");
          }
          &.localisation::before {
            background-image: url("/icons/localisation.svg");
          }
        }
      }
      &.infos {
        position: absolute;
        right: 0;
        top: 50%;
        li {
          margin-left: 1rem;
          &.check {
            p {
              position: relative;
              margin: 0.8rem 4.5rem 0 0;
              font-size: $font-size-base;
              font-family: $regular-heebo;
              color: $raven;
            }
            &::after {
              position: absolute;
              margin-top: -1.8rem;
              right: 1.4rem;
              background-image: url("/icons/check.svg");
              background-repeat: no-repeat;
              background-size: 1.8rem;
              width: 1.8rem;
              height: 1.3rem;
              content: "";
            }
          }
          &.box-candidats-cooptes,
          &.box-views-small {
            background-color: $solitaire;
            padding: 0 1rem 0 4.4rem;
            height: 4rem;
            line-height: 4.3rem;
            font-family: $regular-heebo;
            font-size: 1.3rem;
            border-radius: 0.5rem;
            color: $raven;
            span {
              font-family: $bold-poppins;
              color: $cloud-burst;
              font-size: $font-size-base;
            }
          }
          &.box-candidats-cooptes {
            padding-left: 5rem;
            &::before {
              position: absolute;
              margin: 0.5rem 0 0 -4rem;
              background-image: url("/icons/nouveau-candidat.svg");
              background-repeat: no-repeat;
              background-size: 3rem;
              width: 3rem;
              height: 3rem;
              content: "";
            }
          }
        }
      }
    }
  }
  .box-recompense,
  .box-annonce,
  .box-demande {
    margin: 3rem auto 0 auto;
    height: 9.8rem;
    ul {
      &.titre {
        margin-bottom: 0.5rem;
      }
      &.infos {
        top: -0.7rem;
      }
    }
    &:last-child {
      border-color: transparent;
    }
  }
  .box-annonce {
    max-width: 98rem;
    .box-announce-top-container {
      display: flex;

      .titre {
        flex: 1;
      }
    }
  }
  .box-demande {
    height: 5.8rem;
    max-width: 100%;
  }
  .box-recompense {
    ul.infos {
      top: 2rem;
    }
  }
  .box-annonce {
    ul {
      &.infos {
        top: 1rem;
      }
    }
  }
}

//Cooptation
